// ... (Componente DiagnoseMemberPanel não foi alterado)
function DiagnoseMemberPanel({ currentUser }: { currentUser: Doc<"users"> }) {
  const members = useQuery(api.admin.getUsers, currentUser.terreiroId ? { terreiroId: currentUser.terreiroId, role: "member" } : "skip");
  const events = useQuery(api.events.get, currentUser.terreiroId ? { terreiroId: currentUser.terreiroId } : "skip");
  const baths = useQuery(api.baths.get, currentUser.terreiroId ? { terreiroId: currentUser.terreiroId } : "skip");
  const rituals = useQuery(api.rituals.get, currentUser.terreiroId ? { terreiroId: currentUser.terreiroId } : "skip");
  
  const [selectedMember, setSelectedMember] = useState<Doc<"users"> | null>(null);
  const { register, handleSubmit, reset, watch } = useForm<DiagnosisFormData>();
  const createDiagnosis = useMutation(api.diagnoses.createDiagnosis);
  const diagnosisType = watch("type");

  const onSubmit: SubmitHandler<DiagnosisFormData> = (data) => {
    if (!selectedMember) return;
    createDiagnosis({ ...data, memberId: selectedMember._id });
    reset();
    setSelectedMember(null);
  };
  
  return (
    <section>
      <h2 className="text-2xl font-semibold mb-4 border-b pb-2">Diagnosticar Membros</h2>
      <Dialog open={!!selectedMember} onOpenChange={(isOpen) => !isOpen && setSelectedMember(null)}>
        <div className="space-y-2">
          {members?.map((member) => (
            <div key={member._id} className="flex items-center justify-between p-2 border rounded-lg">
              <span className="font-medium">{member.name} ({member.email})</span>
              <DialogTrigger asChild><Button onClick={() => setSelectedMember(member)}>Diagnosticar</Button></DialogTrigger>
            </div>
          ))}
        </div>
        <DialogContent>
          <DialogHeader><DialogTitle>Novo Diagnóstico para {selectedMember?.name}</DialogTitle></DialogHeader>
          <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
            <div>
              <Label>Tipo de Diagnóstico</Label>
              <Controller name="type" control={{ register, watch } as any} rules={{ required: true }} render={({ field }) => (
                <Select onValueChange={field.onChange} defaultValue={field.value}>
                  <SelectTrigger><SelectValue placeholder="Selecione o tipo..." /></SelectTrigger>
                  <SelectContent>
                    <SelectItem value="event">Evento</SelectItem>
                    <SelectItem value="bath">Banho</SelectItem>
                    <SelectItem value="ritual">Ritual</SelectItem>
                  </SelectContent>
                </Select>
              )} />
            </div>
            {diagnosisType && (
              <div>
                <Label>Item Específico</Label>
                 <Controller name="referenceId" control={{ register, watch } as any} rules={{ required: true }} render={({ field }) => (
                  <Select onValueChange={field.onChange} defaultValue={field.value}>
                    <SelectTrigger><SelectValue placeholder="Selecione o item..." /></SelectTrigger>
                    <SelectContent>
                      {diagnosisType === 'event' && events?.map((item) => <SelectItem key={item._id} value={item._id}>{item.title}</SelectItem>)}
                      {diagnosisType === 'bath' && baths?.map((item) => <SelectItem key={item._id} value={item._id}>{item.title}</SelectItem>)}
                      {diagnosisType === 'ritual' && rituals?.map((item) => <SelectItem key={item._id} value={item._id}>{item.title}</SelectItem>)}
                    </SelectContent>
                  </Select>
                 )} />
              </div>
            )}
             <div><Label htmlFor="notes">Observações</Label><Textarea id="notes" {...register("notes")} /></div>
            <Button type="submit">Salvar Diagnóstico</Button>
          </form>
        </DialogContent>
      </Dialog>
    </section>
  );
}
